.slide-image-container {
    position: relative;
    border: 0px solid black;
    width: 100%;
    height: 100%;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  
  
  .image-caption {
    position: absolute;
    width: 40%;
    min-width: 200px;
    bottom: 50px;
    right: 50px;
    h2, p{
      background-color: rgba(0, 0, 0, 1);
      padding: 10px 10px;
      margin:5px;
      display: inline-block;
      transition: 0.4s all ease-in-out;
      &:hover{
        background-color: white;
        color: black;
        cursor: pointer;
      }
    }
    text-align: left;
  //border: 1px solid black;  
    color: white;
    padding: 10px;
    opacity: 0;
  
  }
  
  .image-caption.active {
    opacity: 1;
    h2, p {
      animation-name: text-animation;
      animation-duration: 1s;
      animation-fill-mode: both;
  
    }
  }
  
  @keyframes text-animation{
    0%{
      opacity: 0; 
      transform: translateY(3rem);
      height: 0%;
      width: 0px;
    }
    50% {
      opacity: 0.2;    
    }
    100% {
      opacity: 1;
      width: auto;
    }
  }